/* Results animations when component mounts / unmounts */

.resultsLeave .results-panel {
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
  will-change: transform;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: -webkit-transform 0.3s cubic-bezier(.07,.82,.64,.97);
  -o-transition: transform 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: transform 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: transform 0.3s cubic-bezier(.07,.82,.64,.97), -webkit-transform 0.3s cubic-bezier(.07,.82,.64,.97);
}

.resultsLeave .results-backdrop {
  opacity: 1;
  will-change: opacity;
  -webkit-transition: opacity 0.3s cubic-bezier(.07,.82,.64,.97);
  -o-transition: opacity 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: opacity 0.3s cubic-bezier(.07,.82,.64,.97);
}

.resultsLeave.resultsLeaveActive .results-panel {
  -webkit-transform: translateY(110%);
      -ms-transform: translateY(110%);
          transform: translateY(110%);
}

.resultsLeave.resultsLeaveActive .results-backdrop {
  opacity: 0;
}

.resultsEnter .results-panel {
  -webkit-transform: translateY(110%);
      -ms-transform: translateY(110%);
          transform: translateY(110%);
  will-change: transform;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: -webkit-transform 0.3s cubic-bezier(.07,.82,.64,.97);
  -o-transition: transform 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: transform 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: transform 0.3s cubic-bezier(.07,.82,.64,.97), -webkit-transform 0.3s cubic-bezier(.07,.82,.64,.97);
}

.resultsEnter .results-backdrop {
  opacity: 0.01;
  will-change: opacity;
  -webkit-transition: opacity 0.3s cubic-bezier(.07,.82,.64,.97);
  -o-transition: opacity 0.3s cubic-bezier(.07,.82,.64,.97);
  transition: opacity 0.3s cubic-bezier(.07,.82,.64,.97);
}

.resultsEnter.resultsEnterActive .results-panel {
  -webkit-transform: translateY(0%);
      -ms-transform: translateY(0%);
          transform: translateY(0%);
}

.resultsEnter.resultsEnterActive .results-backdrop {
  opacity: 1;
}



